@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
.rd-routing-menu {
  z-index: 1000;
  position: absolute;
  width: 500px;
  max-width: 500px;
  flex-grow: 1;
  left: 10px;
  top: 10px;
}

.rd-routing-menu-header {
  display: flex;
}

.rd-mot-checkbox {
  display: flex;
  align-items: center;
}

@media (min-width: 768px) {
  .rd-routing-menu {
    width: 500px;
    max-width: 500px;
  }
}

@media (min-width: 551px) and (max-width: 767px) {
  .rd-routing-menu {
    width: 370px;
    max-width: 370px;
  }
}

@media (min-width: 481px) and (max-width: 550px) {
  .rd-routing-menu {
    width: 350px;
    max-width: 350px;
  }
}

@media (max-width: 480px) {
  .rd-routing-menu {
    width: 280px;
    max-width: 280px;
  }
}

@media (max-width: 420px) {
  .rd-routing-menu {
    width: 280px;
    max-width: 280px;
  }
}
.rd-map-comp {
    height: 100%;
    width: 100%;
}

.ol-zoom {
    right: 10px;
    left: unset;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.rd-pointer {
  cursor: pointer;
}

#root {
  position: absolute;
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

